export default {
	data() {
		return {
			weixin: true,
			payfor:false,
			countdownStr: "00:00",
			//订单信息
			orderInfo: {},
			// 支付金额
			payMoney:0,
			//默认支付方式
			payType: "wxpay",
			// 支付方式
			payData: [{
					id: "wxpay",
					name: "微信支付",
					icon: "hxicon-weixinzhifu",
					icon_color: "#09bb07",
					default: 0,
				},
				{
					id: "alipay",
					name: "支付宝支付",
					icon: "hxicon-zhifubaozhifu",
					icon_color: "#08a0e9",
					default: 1
				}
			],
		}
	},

	onLoad(option) {
		let that = this
		//获取订单id
		// if (option.id) {
		// 	console.log("id+",JSON.stringify(option.id))
		// }
		// 获取支付金额
		// console.log("payMoney+",JSON.stringify(option.payMoney))
		this.payMoney = option.payMoney
	},
	onReady() {
		let that = this
		that.orderInfo = {
			//订单id
			order_id: "LQL20200410134",
			//金额
			money: 206.2,
			//门店名称
			store_name: "小太阳红旗商店",
			//门店地标
			store_community: "玫瑰园店",
			//订单创建时间
			create_date: "2020-04-10 18:08:56",
			//订单有效期
			expiration_date: "2021-04-12 20:06:56"
		}
		that.countdown();

		for (let i in this.payData) {
			if (this.payData[i].default == 1) {
				this.payType = this.payData[i].id
			}
		}
	},

	methods: {
		payChange(event) {
			this.payType = event.target.value
		},
		doOrder() {
			//查看订单
			uni.showModal({
				content: '这是查看订单的事件',
				showCancel: false,
				confirmColor: "#999",
				success: function(res) {
				}
			});
		},

		//倒计时
		countdown() {
			var go_time = new Date((this.orderInfo.expiration_date).replace(/-/g, '/'));
			var now_time = new Date();
			if (!go_time.getTime()) {
				return
			}
			var alltime = go_time.getTime() - now_time.getTime(); //剩余的时间（毫秒）
			if (alltime <= 0) {
				this.countdownStr = ""
				this.showPrompt()
				return
			}
			var haoscend = alltime % 1000; //毫秒
			//console.log(haoscend);
			var scend = parseInt((alltime / 1000) % 60)
			//console.log(scend);
			var minute = parseInt((alltime / 1000 / 60) % 60)
			// console.log(minute);
			var hour = parseInt((alltime / 1000 / 60 / 60) % 24)
			if (scend < 10) {
				scend = "0" + scend
			}
			if (minute < 10) {
				minute = "0" + minute
			}
			let str = minute + ":" + scend;
			if (hour > 0) {
				str = hour + ':' + str
			}
			this.countdownStr = str
			setTimeout(this.countdown, 1000);
		},
		//显示弹窗
		showPrompt() {
			uni.showModal({
				content: '支付超时，请重新下单',
				showCancel: false,
				confirmText: "重新下单",
				confirmColor: "#999",
				success: function(res) {
					if (res.confirm) {

					}
				}
			});
		},
		//确认支付
		submitPay() {
			if (this.countdownStr == '') {
				this.showPrompt()
				return
			}














			//微信小程序
			uni.requestPayment({
				provider: 'wxpay',
				timeStamp: String(Date.now()),
				nonceStr: 'A1B2C3D4E5',
				package: 'prepay_id=wx20180101abcdefg',
				signType: 'MD5',
				paySign: '',
				success: (res)=> {
					console.log('成功:' + JSON.stringify(res));
				},
				fail: (err) => {
					console.log('失败:' + JSON.stringify(err));
					this.payfor=true
				}
			});


		}

	}
}
